<template>
  <div>
    <div class="d-block header-section">
      <div class="">
        <h1>
          <b>Return Order Listing - GRA/TSSP</b>
        </h1>
      </div>
    </div>
    <div class="container-fluid productManagement">
      <!-- Your Details Model -->
      <b-modal id="deactiveItem" hide-footer hide-header centered>
        <div class="d-block">
          <div class="modal-body mt-4 mb-3" align="center">
            <div class="mt-4 mb-4">
              <img src="@/assets/images/icons/warning-message-icon.png" />
            </div>
          </div>
        </div>
      </b-modal>
      <!-- Your Details Model End-->
      <!-- START: Card Data-->
      <div class="clearfix">
      <div class="row float-right">
       <router-link :to="{ name: 'ReturnOrder' }">
          <button 
            type="button"
            class="btn btn-primary float-right mt-3 mb-5 p-2 pl-5 pr-5"
          >
            Create Order
          </button>
        </router-link>
      </div>
      </div>

      <div class="row mb-5">
        <div class="w-100">
          <div class="row">
            <div class="col-12 col-sm-12 mt-3">
              <div class="table-responsive">
                  <table class="table layout-secondary return-order bordered">
                      <thead>
                          <tr>
                              <th>Order Number</th>
                              <th>Manufacturer</th>
                              <th>Date</th>
                              <th>Status</th>
                              <th>Actions</th>
                              
                          </tr>
                      </thead>
                      <tbody v-for="orders in orderListing" :key="orders.orderId">
                          <tr>
                              <td>{{orders.orderNumber}} </td>
                              <td>{{orders.orgName}} </td>
                              <td>{{orders.requestedOn}} </td>
                              <td>{{orders.status }} </td>
                              <td>
                                <router-link  :to="{ name: 'ReturnOrderView', params: { orderId: orders.orderId }}">
                                <span class="mr-4 text-primary"><img src="@/assets/images/icons/view.png" />View</span>
                                </router-link>
                                <span class="mr-4 text-primary"><img src="@/assets/images/icons/edit-small.png" /><a href="#" @click="getdownload(orders.orderNumber)"> Download </a> </span>
                              </td>
                          </tr>
                      </tbody>
                  </table>
                  <b-pagination
                v-model="currentPage"
                :total-rows="this.orderListing.length"
                :per-page="perPage"
                class="float-right mr-4"
                first-text="First"
                prev-text="Prev"
                next-text="Next"
                last-text="Last"
              ></b-pagination>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- END: Card DATA-->
    </div>
  </div>
</template>

<script>
import {getOrderListingApi,getdownloadApi} from "../../services/orderMgmntApis"
export default {
  data() {
    return {
      orderListing: [],
    }
  },
  created() {
    this.getOrderListingGra ();
  },
  methods: {
    getOrderListingGra () {
      let listingPostData ={
        'entityIds': null,
        'orderStatuses': null,
        'ordersFrom': null,
        'ordersTill': null,
        'orgType': null,
        'pageNo': 0,
        'pageSize': 0,
        'searchText': null,
        'sortBy': null,
        'sortByDesc': true,
        'currentPage':1,
        'perPage':10
      }
      getOrderListingApi(listingPostData).then((result) => {
        if ('status' in result && result.status == 200) {
        this.orderListing = result.data.miniReturnOrderDtos
      }
      }).catch((err) => {
        console.log(err)
      });
    },
    getdownload(orderNumber) {
      getdownloadApi(orderNumber).then((result) => {
        console.log(result.data)
      }).catch((err) => {
        console.log(err)
      });
    }
  }
};
</script>